<template>
  <div class="chatview">
        <div  class="icon-wrap">
            <i class="icon iconfont icon-pen-" style="font-size: 24px;"></i>
        </div>
        <div class="line" style="background-color: rgb(128, 95, 245);"></div>
  <div>
    <LoadStatus v-if="a" @statusloaded="statusloaded" />
    <div  v-else class="exercise-container" >
      <div class="header-bar"><span>课堂练习</span></div>
      <div class="code-runner">
        <div class="pannel py-ide">
          <div class="tab-wrap">
            <div class="inner-wrap">
              <div class="file-name">main.py</div>
              <div class="file-line"></div>
            </div>
          </div>
          <textarea id="codeMirrorTextarea" ref="codeMirrorTextarea" class="code-area" v-model="code" ></textarea>
          <div class="operator">
            <div class="run-button .clickable" @click="runPyodide">运行</div>
          </div>
        </div>
        <div class="pannel terminal">
          <div class="terminal-title"> 终端 </div>
          <div class="terminal-xterm">bash:238$<br>{{ output }}</div>
        </div>
    </div>
  </div>

  </div>
  </div>
</template>

<script>
import LoadStatus from './LoadStatus.vue';

export default {
  components: {
        LoadStatus,
    },
  props: {
      chat: Object,
  },
  data() {
    return {
      a:true,
      code: this.chat.code, // 用户输入的代码
      output: "", // 代码执行结果
    };
  },
  updated() {
        this.$emit('scroll-to-bottom');
    }, 
  methods: {
    statusloaded(){
        this.a = false;
        this.$nextTick(() => {
        // 在 DOM 更新后执行操作
          const codeMirrorInstance = window.CodeMirror.fromTextArea(this.$refs.codeMirrorTextarea, {
            mode: 'python',
            theme: 'dracula',
            lineNumbers: true,
          });
          codeMirrorInstance.on('change', () => {
            this.code = codeMirrorInstance.getValue();
          });
        this.runAfterElementLoaded();
      });
    },
    async runPyodide() {
      const PYODIDE_BASE_URL = "https://cdn.jsdelivr.net/pyodide/v0.26.2/full/";
      const pyodide = await window.loadPyodide({ indexURL: PYODIDE_BASE_URL });
      try {
        // 重定向标准输出
        pyodide.runPython(`
          import sys
          from io import StringIO
          sys.stdout = StringIO()
        `);

        // 执行用户输入的代码
        pyodide.runPython(this.code);

        // 获取输出并更新 output 数据属性
        const output = pyodide.runPython("sys.stdout.getvalue()");
        this.output = output;

        // 输出到控制台
        console.log(output);
      } catch (error) {
        // 捕获并显示错误
        this.output = `Error: ${error}`;
      }
    },
  },
  mounted() {
   
    },
  };
</script>


<style>

.exercise-container{
  width:1148px;
  color:white;
}

.header-bar{
  width:100%;
  height:48px;
  background-color:#19171D;
  display:flex;
  align-items:center;
  padding-left:16px;
  color:#71c17c;
  font-size:14px;

}

.code-runner{
  display:flex;
  margin-top:1px;
  flex-direction: row;
}

.pannel{
  width:50%;
  height:260px;
  background-color: #0a080f;
}

.py-ide{
  display:flex;
  flex-direction: column;
  background-color:#282A36;
}

.py-ide ::-webkit-scrollbar {
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 3px;
  background: #201F26;
}

.py-ide ::-webkit-scrollbar-thumb {
    background-color: #313036; /* 设置滑块的背景颜色 */
    border-radius: 3px; /* 设置滑块的边框半径 */
} 

.tab-wrap{
  width:100%;
  height:min-content;
}

.inner-wrap{
  padding:12px 16px;
  width:min-content;
  display:flex;
  flex-direction: column;
  justify-content:center;
  align-items:center;
  font-size:14px;
}


.inner-wrap .file-line{
  width:100%;
  border: 1px solid #805ff5;
  border-radius: 1px;
  margin-top:3px;
}



.CodeMirror{
  width:100%;
  font-size: 15px;
  font-family: "Courier New", Courier, monospace;
  line-height: 1.5;
  flex-grow:1;
}

.operator{
  display:flex;
  align-items:center;
  justify-content:right;
  height:48px;
  background-color: #262530;
  flex-shrink:0;
  flex-grow:0;
  padding:8px 16px;
}



.run-button{
  width:80px;
  height:32px;
  background-color:#6741EF;
  border-radius: 4px;
  transition: all .2s;
  display:flex;
  justify-content:center;
  align-items:center;
  font-size:14px;
  font-family: -apple-system,BlinkMacSystemFont,Helvetica Neue,Arial,PingFang SC,Hiragino Sans GB,Microsoft YaHei,Source Han Sans SC,WenQuanYi Micro Hei,sans-serif;

}

.run-button:hover {
  background-color: #7d5af8;
}
  
.run-button:active {  
  background-color: #34333C;  
  transform: translateY(2px);  
}

.terminal{
  display:flex;
  flex-direction:column;
  padding:0px 8px 16px 20px;
}

.terminal-title{
  color: #fff;
  opacity: .4;
  line-height: 48px;
  height: 48px;
}

.terminal-xterm{
  flex-grow:1;
  font-size: 15px;
  font-family: "Courier New", Courier, monospace;
  line-height: 1.5;
  cursor: text;
  color: #c2c1c6;
  overflow-y:auto;
}


</style>