
<template>
    <p>{{ status }}</p>
</template>

<script>
export default {
    name: 'LoadStatus',
    data(){
        return {
            status: `载入中`,
        }
    },
    methods: {
        init() {
            let count =  0;

            const countdown = setInterval(() => {
                if (count <= 5) {
                    this.status = '载入中' + '.'.repeat(count);
                    count ++;
                } else {
                    count =  0 ;
                }
            }, 150);

            setTimeout(() => {
                clearInterval(countdown);
                this.$emit("statusloaded");
            }, 1500);
        }
    },
    mounted() {
        this.init();
    }
};
</script>


