<template>
    <div class="chatview">
        <div class="icon-wrap">
            <!--  -->
        </div>
        <div class="line" style="background-color: rgba(255, 203, 124, 0.3);"></div>
        <div class="imgview">
            <LoadStatus v-if="a" @statusloaded="statusloaded" />
            <p v-else>
                <img :src="imageSrc" alt="示例图片" />
            </p>
        </div>
    </div>
</template>

<script>
import LoadStatus from './LoadStatus.vue';

    export default{
        components: {
            LoadStatus
        },
        props: {
            chat: Object,
        },
        data(){
            return {
                a: true,
            }
        },
        //计算属性
        computed:{        
            imageSrc(){
                return require(`@/assets/courseimg/${this.chat.img}`)
            }
        },
        mounted() {
        // 在子组件加载完成后，触发父组件的方法
        this.$emit('scroll-to-bottom');
        },
        updated() {
            this.$emit('scroll-to-bottom');
        },    
        methods: {
            statusloaded(){
                this.a = false;
            } 
        },
    }
</script>

<style>

p img{
    width: 640px;
}
</style>