<template>
    <div>
    <div class="chatview">
        <div  class="icon-wrap">
            <i class="icon iconfont icon-pen-" style="font-size: 24px;"></i>
        </div>
        <div class="line" style="background-color: rgb(128, 95, 245);"></div>
        <div class="choiceview">
            <LoadStatus v-if="a" @statusloaded="statusloaded" />
            <div v-else class="choice-container">
                <div class="choice-title">单选题</div>
                <div class="choice-question">{{ chat.question }}</div>
                <ul>
                    <li v-for="(option, index) in chat.options" :key="index">
                        <label class="choice-option" :class="{ selected: selectedIndex === index }">
                            <input type="radio" :value="index" v-model="selectedIndex" :disabled="answerConfirmed" @change="handleOptionChange" />
                            {{ getOptionLabel(index) }}. {{ option }}
                        </label>
                    </li>
                </ul>
            </div>
        </div>  
    </div>  
    <ChatPage class="component" v-if="b" :chat="rechat" />
    </div>
</template>

<script>
import ChatPage from './ChatPage.vue';
import LoadStatus from './LoadStatus.vue';

export default {
    components: {
        LoadStatus,
        ChatPage
    },
    props: {
        chat: Object,
    },

    data() {
        return {
            a: true,
            b: false,
            rechat: '',
            selectedIndex: '',
            answerConfirmed: false
        };
    },
    mounted() {
        // 在子组件加载完成后，触发父组件的方法
        this.$emit('scroll-to-bottom');
    },
    updated() {
        this.$emit('scroll-to-bottom');
    }, 
    methods: {
        handleOptionChange() {
            if (this.answerConfirmed) {
                return; // 如果答案已经确定，不允许再次选择
            }
            this.answerConfirmed = true; // 确定答案后禁用选项
            this.rechat = this.chat.answers[this.selectedIndex]; // 触发自定义事件，并传递选项给父组件// 处理选项变化的逻辑
            this.b=true;
            console.log(this.rechat)
        },
        getOptionLabel(index) {
            return String.fromCharCode(65 + index);
        },
        statusloaded(){
            this.a = false;
        } 
    },

};
</script>

<style>

ul{
    padding: 0;
}
li {
  list-style-type: none; /* 去除样式点 */
}

.selected {
    border: 1px solid #74c17c;
}

.choice-container {
    color: #c2c1c6;

}

.choice-title {
    display: inline-flex;
    padding-left: 6px;
    padding-right: 6px;
    padding-top:0;
    padding-bottom: 0;
    font-size: 14px;
    color: #201F26;
    min-width: 54px;
    background-color: #757377;
    border-radius: 2px;
    align-items: center;
    justify-content: center;
    margin: 5px;
}

.choice-question {
    display: flex;
    color: #F7F7FB;
    padding: 5px;
}

.choice-option {
    display: inline-flex;
    min-width: 640px;
    margin: 5px;
    background-color: #2b2a32;
    border-radius: 7px;
    padding: 16px;
}

input {
    appearance:none;
}

.choiceview .wrapper{
    display: flex;
}

.icon-pen-{
    color: #805ff5;
    line-height: 1;
    background-image: -webkit-gradient(linear,left top,left bottom,from(#fff));
    background-repeat: no-repeat;
    background-size: 60% 60%;
    background-position: 50% 50%;
}

.line {
    width: 2px;
    margin-left: 10px;
}

</style>
