<template>
  <div>
    <textarea ref="codeMirrorTextarea" v-model="code"></textarea>
  </div>
</template>

<script>

export default {

  data() {
    return {
      code: 'print("Hello, World!")',
    };
  },
  mounted() {
    const codeMirrorInstance = window.CodeMirror.fromTextArea(this.$refs.codeMirrorTextarea, {
      mode: 'vue',
      theme: 'dracula',
      lineNumbers: true, // 启用行号显示
    });
    codeMirrorInstance.on('change', () => {
      this.code = codeMirrorInstance.getValue();
    });
  },
};
</script>

<style>
/* Optional: Add styles for the code input area */
.CodeMirror {
  height: auto;
}
</style>