    //自动打印第一句话
    this.addChat();
    //监听全局键盘事件
    window.addEventListener('keydown', this.handleKeyDown);
  },
  destroyed() {
    // 在组件销毁时移除事件监听器
    window.removeEventListener('keydown', this.handleKeyDown);
  },
  //计算属性
  computed: {
    progress() { return `${(this.initialLength-this.chatList.length) * 1180/ this.initialLength}px`}
  },
  updated() {
    this.scrollToBottom();
  },
  methods: {
    addChat() {
        if (this.chatList.length > 0) {
          const chat = this.chatList.shift();
          this.chatArray.push(chat);
        }
    },
    handleKeyDown(event) {
      // 判断按下的键是否是回车键
      if (event.key === 'Enter') {
        // 调用 addChat 函数
        this.addChat();
      }
    },
    scrollToBottom() {
      this.$nextTick(() => {
        const container = this.$refs.chatContainer;
        container.scrollTop = container.scrollHeight;
      });
    },
  },
}
</script>


