<template>
    <div class="chatview" >
        <div class="icon-wrap">
            <img class = "header-img" :src="imageSrc"/>
        </div>
        <div class="line" style="background-color: rgba(255, 203, 124, 0.3);"></div>
        <div class = "msgview">
            <div class = "name" v-show="false"><p>{{ name }}</p></div>
            <div class = "msg">
                <LoadStatus v-if="a" @statusloaded="statusloaded"/>
                <p v-else v-html="msg"></p>
            </div>
        </div>
    </div>
</template>
<script>
import LoadStatus from './LoadStatus.vue';

export default{
    components: {
        LoadStatus
    },
    props: {
        chat: Object
    },
    data(){
        return {
            a: true,
            msg: this.chat.msg,
            name: this.chat.name
        }
    },
    //计算属性
    computed:{
        imageSrc(){
            return require(`@/assets/${this.name}.png`)
        }
    },
    updated() {
        this.$emit('scroll-to-bottom');
    },    
    methods: {
        statusloaded(){
            this.a = false;
        } 
    }

}
</script>

<style>

.chatview{
    display: flex;
    align-items: center;
    overflow: visible;
    align-items:stretch;
    /* border:1px solid red; */
}

.icon-wrap{
    width: 24px;
    flex-shrink: 0;
    display: flex;
    align-items:start;
    padding-top:5px;
}

.header-img{
    width: 100%;
    border-radius: 50%;
}


.line {
    width: 2px;
    margin-left: 10px;
    margin-right:24px;
}

p{
    margin: 0;
    line-height: 2;
}

</style>